<template>
    <div class="postText">
        <van-nav-bar
            title="帖子正文"
            left-text="返回"
            @click-left="back"
            right-text="发帖"
            @click-right="goReleasePost"
            left-arrow
        />
        <div class="box_content">
            <van-tabs v-model="active">
                <van-tab :title="item.title" v-for="(item,index) in taggeet" :key="index">
                   <div class="maxbox">
                        <div class="content_text">
                            <div class="content_title">
                                <div class="content_title_1 on-title">
                                    {{showPostContentData.title}}
                                </div>
                                <div class="content_title_2">
                                    <div class="content_img fl">
                                        <img :src="showPostContentData.userHeadPortrait"/>
                                    </div>
                                    <div class="content_data fl">
                                        <div class="content_Beloved">{{showPostContentData.userNick}}</div>
                                        <div class="content_postTime">
                                            <div class="content_postTime_icon clearfix">
                                                <van-icon name="comment-o" size="20" class="content_postTime_i fl" color="#888"/>
                                                <span>{{showPostContentData.commentNumberContent.length}}</span>
                                            </div>
                                            <div class="content_postTime_icon clearfix">
                                                <van-icon name="star-o" size="20" class="content_postTime_i fl" color="#888"/>
                                                <span>{{showPostContentData.CollectionNumber}}</span>
                                            </div>
                                            <div class="content_postTime_icon clearfix">
                                                <van-icon name="clock-o" size="20" class="content_postTime_i fl" color="#888"/>
                                                <span>{{showPostContentData.openPostDate.date1}}</span>
                                            </div>
                                            <div class="content_postTime_icon clearfix">
                                                <span>{{showPostContentData.openPostDate.date2}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="content-details">
                            {{showPostContentData.userContent}}
                            </div>
                        </div>
                        <!-- 最新评价 -->
                        <div class="newEvaluate" v-if="index==0">
                            <div class="newEvaluate_title">
                                <span>最新评价</span>
                            </div>
                            <div class="newEvaluate_content">
                                <div class="newEvaluate_content_notNum" v-for="(item,index) in showPostContentData.commentNumberContent" :key="index">
                                    <div class="notNum_fl fl">
                                        <img :src="item.userImg"/>
                                    </div>
                                    <div class="notNum_fr fl">
                                        <div class="notNum_fr_item1 fl">{{item.hotNum}}楼</div>
                                       <div class="notNum_fr_item2 fl">
                                            <div class="notNum_title">{{item.userCommentNick}}</div>
                                            <div class="notNum_openTex on-title">{{item.commentText}}</div>
                                            <div class="notNum_time">
                                                <div class="timeDate fl">
                                                    <van-icon name="clock-o" size="20" class="timeDate_icon fl" color="#888"/>
                                                        <span class="fl">{{item.commentDate.date1}}</span>
                                                </div>
                                                <div class="timeDate fl">
                                                        <span>{{item.commentDate.date2}}</span>
                                                </div>
                                            </div>
                                       </div>
                                    </div>
                                    <!-- 用户删除自己的评价 -->
                                    <div class="delSelfPost" v-show="item.showFlag" @click="delTextData(item,index)">
                                        <van-icon
                                            name="delete"
                                            color="#1989fa"
                                            class="item_watch_icon"
                                            size="20"
                                           
                                        />
                                    </div>
                                     <!-- @click.stop="deletePostText(index,item)" -->
                                </div>
                            </div>
                        </div>
                   </div>
                </van-tab>
            </van-tabs>
        </div>
        

        <!-- 底部选项 -->
        <div class="tabbersFooter">
            <van-tabbar v-model="selectActive" @change="onChange">
                <van-tabbar-item :icon="item.icon" v-for="(item,index) in tabbersFooterData" :key="index">{{item.title}}</van-tabbar-item>
               
            </van-tabbar>
        </div>
        <van-share-sheet
        v-model="showShare"
        title="立即分享给好友"
        :options="options"
        />
        <!-- 评论面板 -->
        <van-popup v-model="popupShow" position="bottom" :style="{ height: '30%' }">
            <div class="userOpenInfo">
                <textarea class="userOpenText" placeholder="发布精彩评论(字数不可超过50):" ref="inpText"></textarea>
                <div class="but">
                    <van-button type="primary" block round  @click="openTextData">发布评论</van-button>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
import '../assets/css/PostText.less'
import {createNamespacedHelpers} from 'vuex'

const {mapState,mapMutations} = createNamespacedHelpers('homeModules')

    export default {
        data(){
            return {
               active: 0,
               selectActive: null,
               getSelectPost: {},
               showPostContentData: {},
               taggeet:[{title:"查看全部"},{title: "只看楼主"}],
               showShare: false,
               options: [
                  { name: '微信', icon: 'wechat' },
                  { name: '微博', icon: 'weibo' },
                  { name: '复制链接', icon: 'link' },
                  { name: '分享海报', icon: 'poster' },
                  { name: '二维码', icon: 'qrcode' }
               ],
               tabbersFooterData:[
                   {icon:'chat-o',title: '评论'},
                   {icon:'star-o',title: '收藏'},
                   {icon:'share',title: '分享'}
               ],
            //    评论留言
               popupShow: false,

            }
        },
        created(){
              //获取本地存储数据
                let localSData=localStorage.getItem('pestData');
                localSData=JSON.parse(localSData);
               this.getSelectPost.type = this.$route.query.type;
               this.getSelectPost.num = this.$route.query.num;
              this.getPostTextData();
              if(this.communityData[this.getSelectPost.type][this.getSelectPost.num].uerIsCollection||localSData['communityData'][this.getSelectPost.type][this.getSelectPost.num].uerIsCollection){
                  this.tabbersFooterData.splice(1,1)
              }
        },
        computed:{
            ...mapState(['communityData','myLikePostData'])//myLikeData
        },
        methods:{
            ...mapMutations(['addmyLikePostData','removemyLikePostData','addCollectionNumber','addCommentData','removeCommentData']),
            getPostTextData(){
                 //获取本地存储数据
                let localSData=localStorage.getItem('pestData');
                localSData=JSON.parse(localSData);
                if(localSData!=null){
                     this.showPostContentData=localSData['communityData'][this.getSelectPost.type][this.getSelectPost.num];
                     return;
                }
                 
                 this.showPostContentData=this.communityData[this.getSelectPost.type][this.getSelectPost.num];
                 
            },
            onChange(index){
                  //获取本地存储数据
                let localSData=localStorage.getItem('pestData');
                localSData=JSON.parse(localSData);
                if(this.tabbersFooterData[index].title=="分享"){
                    this.showShare=true;
                    this.selectActive=null;
                }else if(index==1&&this.tabbersFooterData[index].title=="收藏"){
                //    for(let i=0;i<this.myLikeData.length;i++){
                //        if(this.myLikeData[i].){

                //        }
                //    }
                     if(localStorage.getItem('__Tk')==null){
                            this.$dialog.confirm({
                                title: '请先登录再进行收藏',
                                message: '是否立刻进行登录',
                            })
                            .then(() => {
                                this.$router.push({name: "Login"})
                            })
                            .catch(() => {
                                 this.selectActive=null;
                            });
                    }else{
                        this.addCollectionNumber({type: this.getSelectPost.type,index: this.getSelectPost.num});
                        this.showPostContentData=this.communityData[this.getSelectPost.type][this.getSelectPost.num];
                        this.addmyLikePostData(this.communityData[this.getSelectPost.type][this.getSelectPost.num]);
                        this.$toast('收藏成功!');
                        this.tabbersFooterData.splice(index,1);
                        
                        this.selectActive=null;
                    }


                   
                }else if(this.tabbersFooterData[index].title=="评论"){
                    if(localStorage.getItem('__Tk')==null){
                            this.$dialog.confirm({
                                title: '请先登录再进行评论',
                                message: '是否立刻进行登录',
                            })
                            .then(() => {
                                this.$router.push({name: "Login"})
                            })
                            .catch(() => {
                                 this.selectActive=null;
                            });
                    }else{
                        this.popupShow=true;
                        this.selectActive=null;
                    }
                   

                }
                
            },
           back(){
               this.$router.go(-1);
           },
           openTextData(){
              
                let tokenString=localStorage.getItem('__Tk');
                  let localSData=localStorage.getItem('pestData');
                   localSData=JSON.parse(localSData);
                 let date1=new Date();
                let y=date1.getFullYear();
                let m=date1.getMonth()+1;
                let d=date1.getDate();
                let date=y+"-"+m+"-"+d;
                let sumData=date1.getHours()+":"+date1.getMinutes();
                let inpContent=this.$refs.inpText.value;
                if(inpContent.length>20){
                    this.$toast('评论字数不可超过50');
                    return;
                }
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "GET",
                    url: 'http://www.kangliuyong.com:10002/findMy',
                    params:{
                       appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
                       tokenString,
                    }
                }).then(result=>{
                    
                    this.$toast.clear();
                    if(result.data.code=="A001"){
                       let res=result.data.result[0];
                        let o={
                            userCommentNick: res.nickName,
                            userImg: res.userImg,
                            commentDate: {
                                date1: date,
                                date2: sumData
                            },
                            showFlag: true,
                            hotNum: localSData['communityData'][this.getSelectPost.type][this.getSelectPost.num].commentNumberContent.length+1
                        };
                        o.commentText=inpContent;
                        this.popupShow=false;
                        this.selectActive=null;
                        this.addCommentData({type:this.getSelectPost.type,number:this.getSelectPost.num,objContent:o});
                     
                        

                        localSData=localStorage.getItem('pestData');
                        localSData=JSON.parse(localSData);
                        this.showPostContentData=localSData!=null?localSData['communityData'][this.getSelectPost.type][this.getSelectPost.num]:this.communityData[this.getSelectPost.type][this.getSelectPost.num];
                        this.$toast('评论成功');
                    }
                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
     
                

           },
           goReleasePost(){
                if(localStorage.getItem('__Tk')==null){
                            this.$dialog.confirm({
                                title: '请先登录再进行发布帖子',
                                message: '是否立刻进行登录',
                            })
                            .then(() => {
                                this.$router.push({name: "Login"})
                            })
                            .catch(() => {
                                
                            });
                }else{
                     this.$router.push({name: 'ReleasePost'});
                }
              
           },
           delTextData(item,index){
            //     let localSData=localStorage.getItem('pestData');
            //    localSData=JSON.parse(localSData);
               this.removeCommentData({type:this.getSelectPost.type,number:this.getSelectPost.num,index});
                // localSData["communityData"][this.getSelectPost.type][this.getSelectPost.num].commentNumberContent.splice(index,1);
            //    localStorage.setItem('pestData',JSON.stringify(localSData));

               let localSData=localStorage.getItem('pestData');
               localSData=JSON.parse(localSData);
                this.showPostContentData=localSData['communityData'][this.getSelectPost.type][this.getSelectPost.num];
           }
        }
    }
</script>

<style lang="less" scoped>
 
  
</style>